/* Start Challenge CSS */

.start-challenge-page {
  height: 100vw;
  /* background-color: #f8f9fa; */
  display: flex; 
  justify-content: center;
  align-items: start;
}

.start-challenge-page h1{
    color: #f05454;
    margin: 0;
    /* padding: 2vw 0 0 0 */
}

.start-challenge-page h2 {
  color: #f05454
}

.start-challenge-page-content {
  margin: 0;
  padding: 0;
  width: 50vw;
  /* border: 2px solid #6c757d; */
  margin: 3vw;
  padding: 3vw;
  border-radius: 10px;
}

.button-light {
  width: 10vw;
  background-color: #e8e8e8;
  border: 2px solid #f05454;
  border-radius: 5px;
  color: #f05454;
}

.button-dark {
  width: 10vw;
  border-radius: 5px;
}
